function getRandomVerse(min, max) {
            return Math.floor((Math.random() * ((max + 1) - min)) + min);
}

function getRandomVerseRange(targetVal){
    let minMax = {};
    if(isNaN(targetVal)){
        if(targetVal==='whole'){
            minMax.min = bookInfo[0].start;
            minMax.max = bookInfo[65].end;
        } else if (targetVal==='O'){
            minMax.min = bookInfo[0].start;
            minMax.max = bookInfo[38].end;
        } else if (targetVal==='N'){
            minMax.min = bookInfo[39].start;
            minMax.max = bookInfo[65].end;
        } else {
            const group = bookInfo.filter(book=>book.group===targetVal?book:false);
            minMax.min = group[0].start;
            minMax.max = group[(group.length)-1].end;
        }
    } else {
        minMax.min = bookInfo[targetVal].start;
        minMax.max = bookInfo[targetVal].end;
    }
    return minMax;
}

function getRef(verse){
    let theRef = {
        book:'',
        bookID:'',
        chapter:'',
        verse:'',
        youVers:''
    };
    theRef.bookID = bookInfo.findIndex((book) => {
        let matched = false;
        if (book.start <= verse && book.end >= verse) {
            matched = true;
        }
        return matched;
    });    
    theRef.book = bookInfo[theRef.bookID].name;
    theRef.youVers = bookInfo[theRef.bookID].youVersAbb;
    theRef.chapter = bibleBreakdown[theRef.bookID].findIndex((chap)=>{
        let matched = false;
        if (chap.start <= verse && chap.end >= verse) {
            matched = true;
        }
        return matched;
    });
    theRef.verse = verse - bibleBreakdown[theRef.bookID][theRef.chapter].start;
    return theRef;
}

